    export default {
        data() {
            return {
                triggered: false
            }
        },
        onLoad() {
			console.log("1")
            this._freshing = false;
            setTimeout(() => {
                this.triggered = true;
            }, 1000)
        },
		onPullDownRefresh(){
					console.log('刷新中');
					setTimeout(function(){
						uni.stopPullDownRefresh();
						console.log("OK了")
					},2000)
				},
        methods: {
            onPulling(e) {
                console.log("onpulling", e);
            },
            onRefresh() {
                if (this._freshing) return;
                this._freshing = true;
                setTimeout(() => {
                    this.triggered = false;
                    this._freshing = false;
                }, 3000)
            },
            onRestore() {
                this.triggered = 'restore'; // 需要重置
                console.log("onRestore");
            },
            onAbort() {
                console.log("onAbort");
            }
        }
    }
